/* Campo de captura imagen */
#canvas-video-container {
  position: absolute;
  /*Ofixed,segúntunecesidad*/
  left: 50%;
  transform: translateX(-50%);
  top: 250px;
  /*Mantieneelajustevertical*/
  width: 1000px;
  overflow: hidden;
  background-color: #1f4e7c;

}

#canvas-video-container {
  pointer-events: none;
  /* Evita interacción accidental */
}

canvas {
  pointer-events: auto;
  /* Permite interactuar con el contenido dentro */
}



#canvas-video-container>video {
  position: absolute;
  z-index: -2;
  min-height: 50vh;
}

#canvas-video-container>canvas {
  width: 100%;
  background-color: transparent;
  display: block;
  z-index: 1;
  /* padding: 25% 0; */
}

#canvas-video-container>img {
  /* display: none; */
  position: absolute;
  z-index: -1;
}

#canvas-video-container>img.hidden {
  display: none;

}
